/* main */
html {
  font-size: 18px;
  line-height: 1.77778; }

body {
  font-family: "Inter", "Arial", sans-serif;
  color: #3c4c54; }

a {
  text-decoration: none; }

input,
button,
select,
textarea {
  font-family: "Inter", "Arial", sans-serif;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0; }

.container {
  width: 1200px;
  max-width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  box-sizing: border-box; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0; }

/* Страница с компонентами */
.component-wrapper {
  padding: 40px 0;
  background-color: #fff; }
  .component-wrapper + .component-wrapper {
    box-shadow: 0 -1px 0 0 #333; }

/* components */
.title {
  font-size: 36px;
  line-height: 44px;
  font-weight: 600;
  color: #009a9d; }
  .title--upper {
    text-transform: uppercase; }
  .title--dark {
    color: #3c4c54; }
  .title--medium {
    font-size: 32px;
    line-height: 39px; }
  .title--small {
    font-size: 18px;
    line-height: 28px; }

.socials {
  display: inline-flex;
  flex-direction: column;
  border-left: 1px solid #e6f4fa;
  padding-left: 14px;
  padding-bottom: 12px; }
  .socials__item {
    font-size: 12px;
    line-height: 21px;
    text-transform: lowercase; }
    .socials__item a {
      color: #e6f4fa; }
      @media (min-width: 1080px) {
        .socials__item a:hover, .socials__item a:focus {
          text-decoration: underline; } }
  .socials--row {
    flex-direction: row;
    border-left: none;
    padding-left: 0;
    padding-bottom: 0; }
    .socials--row .socials__item {
      font-size: 18px;
      line-height: 22px;
      text-transform: none; }
      @media only screen and (max-width: 767px) {
        .socials--row .socials__item {
          font-size: 16px;
          line-height: 18px; } }
      .socials--row .socials__item:first-child {
        font-weight: 700; }
      .socials--row .socials__item + .socials__item {
        border-left: 1px solid #009a9d;
        margin-left: 13px;
        padding-left: 12px; }
      .socials--row .socials__item a {
        color: #009a9d; }

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  padding: 13px;
  background-color: #009a9d;
  border: 1px solid #009a9d;
  font-size: 18px;
  line-height: 22px;
  font-weight: 600;
  color: #ffffff;
  cursor: pointer;
  box-sizing: border-box;
  transition: all 0.3s ease; }
  @media only screen and (max-width: 767px) {
    .button {
      padding: 8px 13px;
      font-size: 16px;
      line-height: 20px; } }
  @media (min-width: 1080px) {
    .button:hover, .button:focus {
      background-color: rgba(0, 154, 157, 0.85); } }
  .button svg {
    align-self: flex-end;
    width: 26px;
    height: 16px;
    fill: #ffffff;
    margin-left: 10px;
    padding-bottom: 4px; }
  .button--border {
    border-color: #ffffff; }
  .button--white {
    background-color: #ffffff;
    border-color: #009a9d;
    color: #009a9d; }
    @media (min-width: 1080px) {
      .button--white:hover, .button--white:focus {
        background-color: rgba(0, 154, 157, 0.05); } }
    .button--white svg {
      fill: #009a9d; }

.link-arrow {
  font-size: 18px;
  line-height: 22px;
  font-weight: 500;
  text-transform: lowercase;
  color: #009a9d; }
  @media only screen and (max-width: 767px) {
    .link-arrow {
      font-size: 16px;
      line-height: 20px; } }
  @media (min-width: 1080px) {
    .link-arrow:hover svg, .link-arrow:focus svg {
      animation: link-arrow-hover 0.5s linear; } }
  .link-arrow svg {
    width: 47px;
    height: 16px;
    fill: #009a9d;
    margin-left: 6px;
    position: relative;
    top: 3px; }
  .link-arrow--long {
    text-transform: none; }
    .link-arrow--long svg {
      width: 91px;
      height: 16px;
      margin-left: 11px; }

